import React from 'react';
import { Table, Button } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux';
import { addCount, sortName } from '../store';
import Order from '../components/Order';


const Cart = () => {
   const user = useSelector(state => state.user);
   const cart = useSelector(state => state.cart);
   const dispatch = useDispatch() 
   
   return (
      <>
         <h5>장바구니</h5>
        
         <Table>
            <thead>
               <tr>
                  <th>번호</th>
                  <th>상품</th>
                  <th>상품명</th>
                  <th>수량</th>
                  <th>변경하기</th>
               </tr>
            </thead>
            <tbody>
               {
                  cart.map((value, i) =>
                     <tr key={i}>
                        <td>{value.id}</td>
                        <td><img src={process.env.PUBLIC_URL +value.imgUrl} alt="item" width='100px' /></td>
                        <td>{value.item}</td>
                        <td>{value.amount}</td>
                        <td><button onClick={() =>{
                           dispatch(addCount(value.id))
                        }}>+</button></td>
                     </tr>
                  )
               }
            </tbody>
         </Table>
         <Button variant="outline-primary" onClick={() => {
            dispatch(sortName())
         }}>이름순정렬</Button>{' '}

         <Order/>
      </>
   )
}

export default Cart
