import React from 'react'
import { Container, Button } from 'react-bootstrap'
import { useDispatch } from 'react-redux'
import { addItem } from '../store'


const Order = () => {
         let dispatch = useDispatch()
  return (
    <Container style={{marginTop: '50px', textAlign: "center", width: "100%"}}>
        
        <Button style={{fontSize:"20px",padding:"20px",margin:"5px"}} variant="light">선택상품주문</Button>{'  '}
        <Button style={{fontSize:"20px",padding:"20px",margin:"5px"}} variant="light">쇼핑계속하기</Button>{'  '}
         <Button style={{fontSize:"20px",padding:"20px",margin:"5px"}} variant="dark" onClick={()=>{
                  dispatch(addItem({id:3, item:'오렌지',amount:15}))
         }}>전체상품주문</Button>

    </Container>
  )
}

export default Order