import React from 'react'

const Title = (props) => {
         const {title}= props
         const css1= {
                  marginTop:'70px',
                  textAlign:'center',
                  fontSize: '40px'
                  

         }
  return (
    <>
    <h3 style={css1}>{title.tit_h3}</h3>
    <p style={{textAlign:'center',fontSize:'20px',whiteSpace:"pre-wrap"}}>{title.tit_p}</p>
    </>
  )
}

export default Title