import React from 'react'
import { Button } from 'react-bootstrap'
import Slider from 'react-slick';
import '../card.css';


const Cardslide = () => {
         const settings = {
                  dots: true,
                  infinite: true,
                  autoplay: true,
                  autoplaySpeed: 4000,
                  slidesToScroll: 1,
                  slidesToShow: 4
                };
  return (
    <div className='w-4/13 m-auto'>
         <div className='mt-20'>
         <Slider {...settings}>
                  {data.map((d)=>(
                           <div className='cardwr'>
                           <div className='cardwra'>
                                    <img src={process.env.PUBLIC_URL+d.img} alt="im" className='imgg' />
                           </div>
                           <div className='cwr'>
                                    <p className='catx'>{d.title}</p>
                                    <p className='catt'>{d.content}</p>
                                    <Button variant="dark" className='bbttt'>자세히 보기</Button>
                           </div>
                           </div>
                  ))}
                  </Slider>
         </div>

    </div>
  )
}

