import React from 'react'
import { Container, Form, Col, Row, InputGroup } from 'react-bootstrap'
import $ from 'jquery';

const Join = (props) => {

  const mustNumber = (id) => {
    const pattern1 = /[0-9]/;
    const str = $('#' + id).val();
    if (!pattern1.test(str.substr(str.length - 1, 1))) {
      $('#' + id).val(str.substr(0, str.length - 1));
    }
  }

  return (
    <Container>

      <Form.Group method="post" name="frm">
        <Form.Label>이메일</Form.Label>
        <Row>
          <Col xs={6} md={4}>
            <Form.Control id="email_val" type="text" name="is_useremail1" placeholder="이메일을 입력해주세요." /></Col>
          <Col xs={1} md={1}></Col>
          <Col xs={6} md={4}>
            <InputGroup className="mb-2">
              <Form.Select aria-label="Default select example" name="is_useremail2" id="email_val2">
                <InputGroup.Text>@</InputGroup.Text>
                <option>선택하세요</option>
                <option value="1">naver.com</option>
                <option value="2">hanmail.net</option>
                <option value="3">daum.net</option>
              </Form.Select>
            </InputGroup>
          </Col>
        </Row>
        <tr>
          <th>비밀번호</th>
          <td>
            <input type="password" id="pwd_val" name="is_password" placeholder="비밀번호를 입력해주세요." />
          </td>
        </tr>

        <Form.Group as={Col} controlId="formGridZip">
          <Form.Label>핸드폰</Form.Label>
          <Row className="mb-3">
            <Col>
              <Form.Select aria-label="Default select example">
                <option>선택</option>
                <option value="1">010</option>
                <option value="2">011</option>
                <option value="3">111</option>
              </Form.Select>
            </Col>

            <Col>
              <Form.Control max="9999" name="is_u" id="phone2_val" maxLength="4" onChange={(e) => this.mustNumber("phone2_val")} />

            </Col>
            <Col>
              <Form.Control max="9999" name="is_us" id="phone3_val" maxLength="4" onChange={(e) => this.mustNumber("phone3_val")} />
            </Col> </Row>
        </Form.Group>

      </Form.Group>
    </Container>
  )
}

export default Join