import React from 'react'
import { Col, Nav } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom';



const Product = (props) => {

  let navigate = useNavigate();

  let { glass, i, no } = props;



  return (

    <Col md={4}>

      <Nav.Link onClick={() =>

        navigate('/detail/' + no[i])}>

        <img src={process.env.PUBLIC_URL+glass.imgUrl} width="100%" alt='item' />

        <h4>{glass.title}</h4>

        <p>{glass.price}</p>

      </Nav.Link>

    </Col>

  )

}



export default Product



