import React from 'react'
import { Col } from 'react-bootstrap';



const Mejong = (props) => {

  const { mejong } = props


  return (



    <Col md={4}>
      <img src={process.env.PUBLIC_URL+mejong.imgUrl} width='100%' alt='item2' />
      <h5>{mejong.title}</h5>
      <p>{mejong.price}</p>

    </Col>



  )

}



export default Mejong
